
* {
	box-sizing: border-box;
}

body {
	// user-select: none;
	// font-size: 14px;
	// line-height: 1.6;
	display: block;
	letter-spacing: 0.03em;
	margin: 0;
	background-color: #eee2dc;
}

body.dark__theme{
	background-color: rgba(0, 0, 0, 0.863);
}
form {
	display: block;
}
div {
	unicode-bidi: isolate;
}

a,
ul,
ol,
li {
	display: block;
	list-style: none;
	padding: 0;
	margin: 0;
}

a {
	text-decoration: none;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
	margin: 0;
}

img,
svg {
	display: block;
	max-width: 100%;
	height: auto;

	overflow-clip-margin: content-box;
	overflow: clip;
	border: 0;
}
